}
.banner-swiper .swiper-container {
  width: 100%;
  height: 13.125rem;
}
.banner-swiper .swiper-wrapper {
  width: 100%;
  height: 13.125rem;
}
.banner-swiper .swiper-wrapper .adm-swiper-slide {
  width: 100%;
  height: 13.125rem;
  overflow: hidden;
}
.banner-swiper .swiper-wrapper .adm-swiper-indicator .adm-page-indicator-dot {
  width: 1.5625rem;
}
.banner-swiper .carousels {
  position: relative;
  width: 100%;
  overflow: hidden;
  border: none;
}
.banner-swiper .carousels-inner {
  display: flex;
  height: 100%;
  transition: transform 0.3s ease;
}
.banner-swiper .carousels-item {
  flex-shrink: 0;
  width: 100%;
  position: relative;
}
.banner-swiper .carousels-item img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.banner-swiper .carousels-title {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.46875rem;
  background: linear-gradient(transparent, rgba(0, 0, 0, 0.7));
  color: white;
  font-size: 0.5625rem;
  font-weight: 500;
}
.banner-swiper .thumbnails-title {
  padding: 0.25rem;
  font-size: 1rem;
  color: #333;
  background-color: white;
}
.banner-swiper .text-truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}
.banner-swiper .text-clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.banner-swiper .carousels-indicators {
  position: absolute;
  bottom: 0.46875rem;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  gap: 0.1875rem;
  z-index: 10;
}
.banner-swiper .carousels.has-title .carousels-indicators {
  bottom: 1.875rem;
}
.banner-swiper .indicator {
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  background-color: rgba(200, 200, 200, 0.5);
  cursor: pointer;
  transition: all 0.3s ease;
}
.banner-swiper .indicator.active {
  background-color: rgba(144, 144, 144, 0.5);
  width: 0.75rem;
  border-radius: 0.125rem;
}
.banner-swiper .carousels-control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease, background-color 0.3s ease;
  z-index: 10;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.banner-swiper .carousels:hover .carousels-control {
  opacity: 1;
}
.banner-swiper .carousels-control:hover {
  background-color: rgba(0, 0, 0, 0.5);
}
.banner-swiper .control-prev {
  left: 0.46875rem;
}
.banner-swiper .control-next {
  right: 0.46875rem;
}
.banner-swiper .thumbnails-container {
  display: flex;
  gap: 0.3125rem;
  margin-top: 0.46875rem;
  padding: 0.3125rem 0;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: #ccc transparent;
}
.banner-swiper .thumbnails-container::-webkit-scrollbar {
  height: 0.1875rem;
}
.banner-swiper .thumbnails-container::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 0.09375rem;
}
.banner-swiper .thumbnail {
  width: 3.75rem;
  border-radius: 0.125rem;
  overflow: hidden;
  cursor: pointer;
  opacity: 0.7;
  transition: all 0.3s ease;
  flex-shrink: 0;
  background-color: #f5f5f5;
}
.banner-swiper .thumbnail.active {
  opacity: 1;
  transform: scale(1.03);
  box-shadow: 0 0.0625rem 0.25rem rgba(0, 0, 0, 0.2);
}
.banner-swiper .thumbnail-image {
  width: 100%;
  height: 2.5rem;
  -o-object-fit: cover;
     object-fit: cover;
  display: block;
}
